import axios from "axios";
import { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import "./Earning.css";

const DashboardEarning = () => {
  const [earnings, setEarnings] = useState([]);
  const [totalEarnings, setTotalEarnings] = useState(0);
  const [monthlyEarnings, setMonthlyEarnings] = useState([]);

  // Dropdown states
  const [selectedMonth, setSelectedMonth] = useState("");
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  // Month list
  const months = [
    { value: "January", label: "January" },
    { value: "February", label: "February" },
    { value: "March", label: "March" },
    { value: "April", label: "April" },
    { value: "May", label: "May" },
    { value: "June", label: "June" },
    { value: "July", label: "July" },
    { value: "August", label: "August" },
    { value: "September", label: "September" },
    { value: "October", label: "October" },
    { value: "November", label: "November" },
    { value: "December", label: "December" },
  ];

  // ✅ Fetch daily stats (default last 7 days or filtered by month)
  const fetchEarnings = async (month = "", year = new Date().getFullYear()) => {
    try {
      const centreId = localStorage.getItem("centreId");
      if (!centreId) return console.error("No centreId found in localStorage");

      let url = `${process.env.REACT_APP_BACKEND_URL}/api/bookings/centre/${centreId}/stats-date`;

      // If month selected, append query
      if (month) {
        url += `?month=${month}&year=${year}`;
      }

      const res = await axios.get(url);
      setEarnings(res.data.stats);
      setTotalEarnings(res.data.totalPlatformFees);
    } catch (error) {
      console.error("Error fetching earnings:", error);
    }
  };

  // ✅ Fetch monthly earnings for chart
  const fetchMonthlyEarnings = async () => {
    try {
      const centreId = localStorage.getItem("centreId");
      if (!centreId) return console.error("Center ID is not in local storage");

      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/bookings/monthly/${centreId}`
      );
      setMonthlyEarnings(res.data.stats || []);
    } catch (error) {
      console.error("Error to fetch monthly details", error);
    }
  };

  // Chart options
  let options = {
    chart: { id: "monthly-earnings", toolbar: { show: false } },
    xaxis: {
      categories: monthlyEarnings.map((item) => item.month),
      title: { text: "Month" },
    },
    yaxis: {
      // title: { text: "Earnings (₹)" },
      labels: { formatter: (val) => `₹${val}` },
    },
    stroke: { curve: "smooth" },
    dataLabels: {
      enabled: true,
      formatter: (val) => `₹${val}`,
    },
    tooltip: {
      y: { formatter: (val) => `₹${val}` },
    },
  };

  let series = [
    {
      name: "Earnings",
      data: monthlyEarnings.map((item) => item.earnings || 0),
    },
  ];

  // Load default on mount
  useEffect(() => {
    fetchEarnings();
    fetchMonthlyEarnings();
  }, []);

  // Handle month/year change
  const handleMonthChange = (e) => {
    const month = e.target.value;
    setSelectedMonth(month);
    fetchEarnings(month, selectedYear);
  };

  const handleYearChange = (e) => {
    const year = e.target.value;
    setSelectedYear(year);
    if (selectedMonth) {
      fetchEarnings(selectedMonth, year);
    }
  };

  return (
    <div className="dashboard-body__content">
      <div className="card common-card">
        <div className="card-body">
          <div className="row gy-4">
            {/* Chart */}
            <div className="col-lg-12">
              <div className="dashboard-card">
                <div className="dashboard-card__header">
                  <h6 className="dashboard-card__title mb-0">
                    Monthly Earning
                  </h6>
                </div>
                <Chart
                  options={options}
                  series={series}
                  type="area"
                  width={"100%"}
                  height={"100%"}
                />
              </div>
            </div>

            {/* Filters + Table */}
            <div className="col-lg-12">
              <div className="card common-card border shadow-none border-gray-five">
                <div className="card-body">
                  <div className="filter-header">
                    {/* Month & Year Filters */}
                    <div className="filters">
                      <select
                        value={selectedMonth}
                        onChange={handleMonthChange}
                      >
                        <option value="">-- Select Month --</option>
                        {months.map((m) => (
                          <option key={m.value} value={m.value}>
                            {m.label}
                          </option>
                        ))}
                      </select>

                      <select value={selectedYear} onChange={handleYearChange}>
                        {[2025].map((year) => (
                          <option key={year} value={year}>
                            {year}
                          </option>
                        ))}
                      </select>
                    </div>

                    {/* Total */}
                    <div className="total">
                      <h6>Total amount to be paid: {totalEarnings}</h6>
                    </div>
                  </div>

                  {/* Table */}
                  <div
                    className="table-responsive"
                    style={{ maxHeight: "800px", overflowY: "auto" }}
                  >
                    <table className="table text-body mt--24">
                      <thead>
                        <tr>
                          <th>Date</th>
                          <th>Appointments</th>
                          <th>My Earning</th>
                          <th>Platform Fees</th>
                        </tr>
                      </thead>
                      <tbody>
                        {earnings?.length > 0 ? (
                          (() => {
                            // Apply filter only when month is selected
                            const filteredData = selectedMonth
                              ? earnings.filter(
                                  (earn) => earn.completedAppointments > 0
                                )
                              : earnings;

                            if (filteredData.length === 0) {
                              return (
                                <tr>
                                  <td colSpan="4" className="text-center">
                                    {selectedMonth
                                      ? `No completed appointments in ${selectedMonth} ${selectedYear}`
                                      : "No records found"}
                                  </td>
                                </tr>
                              );
                            }

                            return filteredData.map((earn, index) => (
                              <tr key={index}>
                                <td>{earn.date}</td>
                                <td>{earn.completedAppointments}</td>
                                <td>{earn.yourEarnings}</td>
                                <td>{earn.platformFees}</td>
                              </tr>
                            ));
                          })()
                        ) : (
                          <tr>
                            <td colSpan="4" className="text-center">
                              No earnings data available
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
            {/* End Table */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashboardEarning;
